
.container {
    max-width: 1246px;
    margin: 0px auto;
    padding: 0px 15px;
    transition: 500ms;
    
    @media screen and (min-width: 362px) {
        width: 362px;
    }
    @media screen and (min-width: 728px) {
        width: 728px;
    }
    @media screen and (min-width: 1246px) {
        width: 1246px;
    }
}