html, body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
  box-sizing: border-box;
}

address {
    font-style: normal;
}

button {
    border: none;
    cursor: pointer;
}

img {
    width: 100%;
    max-width: 100%;
    height: auto;
    display: block;
}

h1, h2, h3, h4, h5, h5, h6, p {
    margin: 0;
    padding: 0;
    font-weight: 400;
}