  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

html {
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}

body {
