.backdrop {
  z-index: 100;
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  padding: 16px;
  transition: opacity .25s, visibility .25s;
  position: fixed;
  inset: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop__inner {
  max-width: 480px;
  margin: 0 auto;
}

.backdrop__top {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  display: flex;
}

.backdrop__close {
  color: #fff;
  cursor: pointer;
  background: #1c2540;
  border: none;
  border-radius: 999px;
  width: 32px;
  height: 32px;
  font-size: 18px;
}

.backdrop__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.backdrop__item:not(:last-child) {
  margin-bottom: 20px;
}

.backdrop__link {
  color: #1c2540;
  font-size: 20px;
  text-decoration: none;
  transition: width .25s ease-in-out;
  display: inline-block;
  position: relative;
}

.backdrop__list > .backdrop__item:not(:last-child) > .backdrop__link:after {
  content: "";
  border-bottom: 2px solid #f15525;
  border-radius: 2px;
  width: 0%;
  transition: width .25s ease-in-out;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.backdrop__list > .backdrop__item:not(:last-child) > .backdrop__link:hover:after {
  width: 100%;
}

.backdrop__link:hover {
  opacity: .8;
}

.backdrop__link--btn {
  border: 1px solid #1c2540;
  border-radius: 10px;
  padding: 10px 18px;
}

.no-scroll {
  overflow: hidden;
}
